.about {
  background-color: white;
  padding: 100px 0;
  /* background: rgb(75, 53, 118); */
    background: linear-gradient(
      219deg,
      rgba(75, 53, 118, 1) 33%,
      rgba(248, 177, 60, 1) 100%
    );
}

.container {
  max-width: auto;
  margin: auto;
  /* padding: 0 0px; */
}

.section-title {
  text-align: center;
  margin-bottom: 40px;
  text-transform: uppercase;
}

.social-icon {
  display: flex;
  justify-content: center;

  align-items: center;
  padding: 10px;
}
.social-icon a {
  margin: 10px;
  background-color: #d6cfcf;
  font-size: 40px;
  margin: auto;
  border-radius: 50%;
  border: 6px solid whitesmoke;
  box-shadow: 2px 2px 10px 2px rgba(0, 0, 0, 0.5);
  margin-bottom: 1rem;
  transition: filter 0.4s ease-in-out;
  filter: grayscale();
}
.social-icon a:hover:nth-child(1) {
  filter: none;
  background-color: #d11212;
}
.social-icon a:hover:nth-child(2) {
  filter: none;
  background-color: #3d10dd;
}
.social-icon a:hover:nth-child(3) {
  filter: none;
  background-color: #e2d9d9;
}
.about-content {
  display: flex;
  flex-direction: row;
  margin-left: 300px;

  align-items: center;
}

.about-text {
  max-width: 600px;
  text-align: center;
  margin-bottom: 0px;
  text-transform: capitalize;
  font-size: 20px;
  margin-left: 20px;
}

.about-text p {
  font-size: 30px;
}
.wave {
  animation-name: wave-animation; /* Refers to the name of your @keyframes element below */
  animation-duration: 0.5s; /* Change to speed up or slow down */
  animation-iteration-count: infinite; /* Never stop waving :) */
  transform-origin: 70% 70%; /* Pivot around the bottom-left palm */
  display: inline-block;
}

@keyframes wave-animation {
  0% {
    transform: rotate(0deg);
  }
  10% {
    transform: rotate(14deg);
  } /* The following five values can be played with to make the waving more or less extreme */
  20% {
    transform: rotate(-8deg);
  }
  30% {
    transform: rotate(14deg);
  }
  40% {
    transform: rotate(-4deg);
  }
  50% {
    transform: rotate(10deg);
  }
  60% {
    transform: rotate(0deg);
  } /* Reset for the last half to pause */
  100% {
    transform: rotate(0deg);
  }
}

.about-text ul {
  margin-top: 20px;
  margin-bottom: 20px;
}
.about-text ul li {
  display: flex;
}

.sub-heading {
  font-size: 40px;
  margin-bottom: 20px;
  text-transform: uppercase;

  background-image: linear-gradient(
    -225deg,
    #231557 0%,
    #44107a 29%,
    #ff1361 67%,
    #fff800 100%
  );
  background-size: auto auto;
  background-clip: border-box;
  background-size: 200% auto;
  color: #fff;
  background-clip: text;
  scrollbar-color: transparent;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  animation: textclip 2s linear infinite;
  display: inline-block;
}

@keyframes textclip {
  to {
    background-position: 200% center;
  }
}

.about-image {
  text-align: center;
  margin-left: 20px;
}

.profile-image {
  width: 300px;
  border-radius: 50%;
  border: 6px solid rgb(25, 27, 105);
  box-shadow: 2px 2px 10px 2px rgba(0, 0, 0, 0.5);
  margin-bottom: 1rem;
  transition: filter 0.4s ease-in-out;
  filter: grayscale();
}
.profile-image:hover {
  filter: none;
}

h1 {
  color: #333;
  font-family: tahoma;
  font-size: 3rem;
  font-weight: 100;
  line-height: 1.5;
  text-transform: uppercase;
  white-space: nowrap;
  overflow: hidden;
  position: relative;
  width: 550px;
}

h1 span {
  font-weight: 500;
  font-size: 40px;
  margin-right: 300px;
}

@media (max-width: 768px) {
  .about-content {
    margin: auto;
  }
}

@media (max-width: 768px) {
  .about-content {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
}
